.keypad {
  flex: 1.3;
  background-color: var(--bg-color-2);
  padding: 20px;
  display: flex;
}
.keypad p {
  line-height: 24px;
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 200ms;
  user-select: none;
  cursor: pointer;
  color: var(--text-color-2);
}
.keys {
  flex: 3;
  display: flex;
  flex-wrap: wrap;
}
.keys p {
  width: 33%;
  height: 25%;
}
.keys p:active {
  background-color: rgba(0, 0, 0, 0.14);
}
.symbols {
  flex: 1;
}
.symbols p {
  color: #89a5f1;
  width: 100%;
  height: 20%;
}
